import React, { Component } from 'react'
import Link from 'next/link'

export default class FooterContentLink extends Component {
    
    render() {
        return (
            <div>
                <i className={this.props.icon}></i>
                <span>
                    <Link href={this.props.href}>{this.props.text}</Link>
                </span>
            </div>
        )
    }
}
